<template>
  <div class="user_header">
    <span style="margin-left: -250px" @click="backHome">
      <i class="el-icon-cloudy"> OurSystem</i></span
    >
    <span style="margin-right: -250px"
      ><label for="more">{{ this.user.nickname }}</label>
      <div class="block">
        <el-avatar :size="50" :src="this.user.avater"></el-avatar>
      </div>
      <van-popover
        v-model="showPopover"
        trigger="click"
        :actions="actions"
        @select="select"
      >
        <template #reference>
          <i class="el-icon-more-outline"></i>
        </template>
      </van-popover>
    </span>
  </div>
</template>

<script>
export default {
  name: "Header",

  data() {
    return {
      user: this.$store.getters.getUser,
      showPopover: false,
      actions: [
        { text: "个人信息", icon: "user-o" },
        { text: "退出登录", icon: "warn-o" },
      ],
    };
  },

  mounted() {},

  methods: {
    select(action, index) {
      if (index === 0) {
        this.$router.push("/personalCenter");
      } else {
        this.logout();
      }
    },
    backHome() {
      this.$router.push("/home");
    },
    logout() {
      this.$confirm("确定退出登录吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.$axios
          .get("/logout")
          .then((res) => {
            this.$store.commit("REMOVE_INFO");
            this.$router.replace("/home");
          })
          .then(() => {
            this.$message({
              type: "success",
              message: "退出成功",
            });
          });
      });
    },
  },
};
</script>

<style lang="less" scoped>
.user_header {
  margin: 0;
  padding: 0;
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
i {
  font-style: italic;
}
span {
  display: flex;
  align-items: center;
  justify-content: center;
  /deep/.el-avatar {
    margin: 0 20px;
    img {
      width: 100%;
    }
  }
}
/deep/.van-popover__wrapper {
  margin-left: 15px;
}
</style>